import React from 'react';
import ReactPlayer from 'react-player/youtube';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { Button, ButtonGroup, ButtonToolbar } from 'react-bootstrap';
import { confirm as addSongToTabActivedDialog } from "./AddSongToTabActivedDialog";
import { confirm as addTabDialog } from "./AddTabDialog";

class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            url: "",
            pip: false,
            playing: false,
            controls: localStorage.mySettings === undefined ? false : JSON.parse(localStorage.mySettings).controls,
            light: localStorage.mySettings === undefined ? false : JSON.parse(localStorage.mySettings).light,
            volume: localStorage.mySettings === undefined ? 0.5 : JSON.parse(localStorage.mySettings).volume,
            muted: localStorage.mySettings === undefined ? false : JSON.parse(localStorage.mySettings).muted,
            played: 0,
            loaded: 0,
            duration: 0,
            playbackRate: localStorage.mySettings === undefined ? 1.0 : JSON.parse(localStorage.mySettings).playbackRate,
            loop: false,
            playIndex: localStorage.mySettings === undefined ? {album: 0, index: 0} : JSON.parse(localStorage.mySettings).playIndex,
            viewIndex: localStorage.mySettings === undefined ? {album: 0, index: null} : JSON.parse(localStorage.mySettings).viewIndex,
            list: localStorage.myList === undefined ? [{tabName: "default", songList: []}] : JSON.parse(localStorage.myList),
            playMode: localStorage.mySettings === undefined ? 0 : JSON.parse(localStorage.mySettings).playMode,
            playModeSymbol: localStorage.mySettings === undefined ? "🔁" : JSON.parse(localStorage.mySettings).playModeSymbol,
            jsonUrl: "data:application/json;charset=utf-8," + encodeURIComponent(JSON.stringify(localStorage.myList === undefined ? [{tabName: "default", songList: []}] : JSON.parse(localStorage.myList),))
        };
        this.handleAddSongToTabActivedDialog = this.handleAddSongToTabActivedDialog.bind(this);
        this.handleAddTabDialog = this.handleAddTabDialog.bind(this);
        this.fileRef = React.createRef();
    }

    loadFile = () => {
        const reader = new FileReader()
        reader.onload = (event) => {
            let readContent;
            try {
                readContent = JSON.parse(event.target.result);
                this.setState({ list: readContent });
                this.save2LocalStorage();
            } catch (e) {
                console.log(e.toString());
            }
        }
        reader.onerror = (error) => {
            //reject(error)
        }
        reader.readAsText(this.fileRef.current.files[0], "UTF-8");
    }

    load = (url) => {
        this.setState({
        url,
        played: 0,
        loaded: 0,
        pip: false,
        playing: true,
        });
    }

    loadFromList = (index) => {
        this.handleTogglePlaying(index);
        this.load("https://www.youtube.com/watch?v=" + this.state.list[index.album].songList[index.index].vid);
    }

    handleChangeList = () => {
        this.setState({jsonUrl: "data:application/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.list))});
    }

    handleChangePlayMode = () => {
        switch (this.state.playMode) {
            case 0:
                this.setState({playMode: 1, playModeSymbol: "🔀"});
                break;
            case 1:
                this.setState({playMode: 2, playModeSymbol: "🔂"});
                break;
            case 2:
                this.setState({playMode: -1, playModeSymbol: "➡"});
                break;
            default:
                this.setState({playMode: 0, playModeSymbol: "🔁"});
        }
        this.save2LocalStorage();
    }

    handlePlayPause = () => {
        const playIndex = this.state.playIndex;
        if (this.state.url === "" && this.state.list[playIndex.album].songList.length > 0) {
            this.setState({url: "https://www.youtube.com/watch?v=" + this.state.list[playIndex.album].songList[playIndex.index].vid});
        }
        this.setState({ playing: !this.state.playing })
    }

    handleNext = () => {
        this.nextSong();
    }

    handleStop = () => {
        this.setState({ playing: false })
    }

    handleToggleControls = () => {
        const url = this.state.url
        this.setState({
        controls: !this.state.controls,
        url: null
        }, () => this.load(url))
    }

    handleToggleLight = () => {
        this.setState({ light: !this.state.light })
    }

    handleToggleLoop = () => {
        this.setState({ loop: !this.state.loop })
    }

    handleVolumeChange = e => {
        this.setState({ volume: parseFloat(e.target.value) })
        this.save2LocalStorage();
    }

    handleToggleMuted = () => {
        this.setState({ muted: !this.state.muted })
        this.save2LocalStorage();
    }

    handleSetPlaybackRate = e => {
        this.setState({ playbackRate: parseFloat(e.target.value) })
    }

    handleTogglePIP = () => {
        this.setState({ pip: !this.state.pip })
    }

    handlePlay = () => {
        console.log('onPlay')
        this.setState({ playing: true })
    }

    handleEnablePIP = () => {
        console.log('onEnablePIP')
        this.setState({ pip: true })
    }

    handleDisablePIP = () => {
        console.log('onDisablePIP')
        this.setState({ pip: false })
    }

    handlePause = () => {
        console.log('onPause')
        this.setState({ playing: false })
    }

    handleSeekMouseDown = e => {
        this.setState({ seeking: true })
    }

    handleSeekChange = e => {
        this.setState({ played: parseFloat(e.target.value) })
        this.save2LocalStorage();
    }

    handleSeekMouseUp = e => {
        this.setState({ seeking: false })
        this.player.seekTo(parseFloat(e.target.value))
    }

    handleProgress = state => {
        //console.log('onProgress', state)
        // We only want to update time slider if we are not currently seeking
        if (!this.state.seeking) {
            this.setState(state)
        }
        this.save2LocalStorage();
    }

    handleEnded = () => {
        console.log('onEnded')
        if (this.state.loop) {
            this.setState({ playing: this.state.loop })
        } else {
            this.nextSong();
        }
    }

    nextSong = () => {
        const playIndex = this.state.playIndex;
        const list = this.state.list;
        switch (this.state.playMode) {
            case 0:
                this.loadFromList({album: playIndex.album, index: (playIndex.index + 1) === list[playIndex.album].songList.length ? 0 : playIndex.index + 1});
                break;
            case 1:
                let nextIndex = Math.floor(Math.random() * Math.floor(list[playIndex.album].songList.length));
                while (nextIndex === playIndex.index && list[playIndex.album].songList.length > 1) {
                    nextIndex = Math.floor(Math.random() * Math.floor(list[playIndex.album].songList.length));
                }
                this.loadFromList({album: playIndex.album, index: nextIndex});
                break;
            case 2:
                this.setState({ playing: true })
                break;
            default:
                this.setState({ playing: false })
        }
        this.save2LocalStorage();
    }

    handleDuration = (duration) => {
        console.log('onDuration', duration)
        this.setState({ duration })
    }

    handleChangeTab = (index) => {
        if (this.state.viewIndex.album !== index) {
            this.setState({viewIndex: { album: index, index: null }});
            this.save2LocalStorage();
        }
    }

    handleToggleSingleClick = (viewIndex) => {
        this.setState({ viewIndex: viewIndex });
        this.save2LocalStorage();
    };

    handleTogglePlaying = (playIndex) => {
        this.setState({
            playIndex: playIndex,
            viewIndex: {album: playIndex.album, index: null}
        });
        this.save2LocalStorage();
    };

    handleAddTabDialog () {
        addTabDialog()
        .then(({output}) => {
            if (/\S/.test(output)) {
                this.setState({
                    list: [...this.state.list, {tabName: output, songList: []}],
                });
                this.save2LocalStorage();
                this.handleChangeList();
            }
        }, () => {
        });
    }

    handleEditTabDialog (name, index) {
        addTabDialog({name: name})
        .then(({output}) => {
            if (/\S/.test(output)) {
                let tmpList = this.state.list;
                tmpList[index].tabName = output;
                this.setState({
                    list: tmpList,
                });
                this.save2LocalStorage();
                this.handleChangeList();
            }
        }, () => {
        });
    }

    handleAddSongToTabActivedDialog () {
        addSongToTabActivedDialog()
        .then((output) => {
            let vidArr = output.vid.match(/(?<=^https:\/\/youtu.be\/|^https:\/\/www.youtube.com\/watch\?(.*?)(^|\/|v=)|^)[a-z0-9_-]{11}/gim);
            const url = "https://www.youtube.com/watch?v=" + (vidArr == null ? "" : vidArr[0]);
            if (ReactPlayer.canPlay(url)) {
                let tmpList = this.state.list;
                tmpList[this.state.playIndex.album].songList = [...tmpList[this.state.playIndex.album].songList, {vid: vidArr[0], title: output.title}]
                this.setState({
                    list: tmpList,
                });
                this.save2LocalStorage();
                this.handleChangeList();
            }
        }, () => {
        });
    }

    handleEditSongDialog (title, vid, index) {
        addSongToTabActivedDialog({title: title, vid: vid})
        .then((output) => {
            let vidArr = output.vid.match(/(?<=^https:\/\/youtu.be\/|^https:\/\/www.youtube.com\/watch\?(.*?)(^|\/|v=)|^)[a-z0-9_-]{11}/gim);
            const url = "https://www.youtube.com/watch?v=" + (vidArr == null ? "" : vidArr[0]);
            if (ReactPlayer.canPlay(url)) {
                let tmpList = this.state.list;
                tmpList[index.album].songList[index.index] = {vid: output.vid, title: output.title};
                this.setState({
                    list: tmpList,
                });
                this.save2LocalStorage();
                this.handleChangeList();
            }
        }, () => {
        });
    }

    formatTime = (playTime, duration) => {
        const durationDate = new Date(duration * 1000);
        const durationHours = ("0" + durationDate.getUTCHours()).slice(-2);
        const durationMinutes = ("0" + durationDate.getUTCMinutes()).slice(-2);
        const durationSeconds = ("0" + durationDate.getUTCSeconds()).slice(-2);
        const playTimeDate = new Date(playTime * 1000);
        const playTimeHours = ("0" + playTimeDate.getUTCHours()).slice(-2);
        const playTimeMinutes = ("0" + playTimeDate.getUTCMinutes()).slice(-2);
        const playTimeSeconds = ("0" + playTimeDate.getUTCSeconds()).slice(-2);
        if (durationHours !== "00") {
            return `${playTimeHours}:${playTimeMinutes}:${playTimeSeconds}/${durationHours}:${durationMinutes}:${durationSeconds}`;
        }
        return `${playTimeMinutes}:${playTimeSeconds}/${durationMinutes}:${durationSeconds}`;
    }

    renderTime = (played) => {
        return this.formatTime(played * this.state.duration, this.state.duration);
    }

    renderSongList = () => {
        const viewIndex = this.state.viewIndex;
        const playIndex = this.state.playIndex;
        const songs = this.state.list[viewIndex.album].songList.map((item, index) => 
            <tr key={index}>
                <td
                    className={`${playIndex.album === viewIndex.album && playIndex.index === index ? "playing" : ""} ${viewIndex.index === index && !(playIndex.album === viewIndex.album && playIndex.index === index) ? "selected" : ""}`}
                >
                    <div className="title"
                         onClick={() => this.handleToggleSingleClick({album: this.state.viewIndex.album, index: index})}
                         onDoubleClick={() => {
                            this.handleTogglePlaying({album: this.state.viewIndex.album, index: index});
                            this.load("https://www.youtube.com/watch?v=" + item.vid);
                         }}
                    >{item.title}</div>
                    <ButtonGroup size="sm" aria-label={index} className="song-control">
                        <Button variant="secondary" onClick={() => {
                            this.handleEditSongDialog(item.title, item.vid, {album: this.state.viewIndex.album, index: index});
                        }}>Edit</Button>
                        <Button variant="danger" onClick={() => {
                            this.state.list[this.state.viewIndex.album].songList.splice(index, 1);
                            this.setState({list: this.state.list});
                            this.save2LocalStorage();
                            this.handleChangeList();
                        }}>x</Button>
                    </ButtonGroup>
                </td>
            </tr>
        )
        return (songs)
    }

    renderTabList = () => {
        const tabs = this.state.list.map((item, index) => 
                <ButtonGroup size="sm" aria-label={index} key={index}>
                    <Button variant={index === this.state.viewIndex.album ? 'light' : 'dark'}
                            onClick={() => { this.handleChangeTab(index); }}
                            onDoubleClick={() => {this.handleEditTabDialog(item.tabName, index)}}
                            >{item.tabName}</Button>
                    <Button variant="danger" onClick={() => {
                        this.state.list.splice(index, 1);
                        if (index === this.state.viewIndex.album) {
                            this.setState({viewIndex: {album: 0, index: null}});
                        }
                        this.setState({list: this.state.list});
                        this.save2LocalStorage();
                        this.handleChangeList();
                    }}
                    disabled={!(this.state.list.length > 1)}
                    >x</Button>
                </ButtonGroup>
        )
        return (tabs)
    }

    save2LocalStorage = () => {
        const {controls, light, volume, muted, playbackRate, playIndex, viewIndex, list, playMode, playModeSymbol, played} = this.state;
        localStorage.setItem("myList", JSON.stringify(list));
        localStorage.setItem("mySettings", JSON.stringify({controls:controls, light:light, volume:volume, muted:muted, playbackRate:playbackRate, playIndex:playIndex, viewIndex:viewIndex, playMode:playMode, playModeSymbol:playModeSymbol, played:played}));
    }

    ref = player => {
        this.player = player
    }

    render () {
        const { url, playing, controls, light, volume, muted, loop, played, playbackRate, pip, playModeSymbol } = this.state;

        return (
            <div>
                <div className="control-panel">
                    <div className="controller">
                        <div className="toolbar">
                            <div className="toolbar-component">
                                <span onClick={this.handlePlayPause}>{playing ? "⏸" : "▶"}</span>
                            </div>
                            <div className="toolbar-component">
                                <span onClick={this.handleNext}>⏭</span>
                            </div>
                            <div className="toolbar-component">
                                <span onClick={this.handleChangePlayMode}>{playModeSymbol}</span>
                            </div>
                            <div className="toolbar-component">
                                <span style={{color:"white"}}>{this.renderTime(played)} </span>
                                <input
                                    type='range' min={0} max={0.999999} step='any'
                                    value={played}
                                    onMouseDown={this.handleSeekMouseDown}
                                    onChange={this.handleSeekChange}
                                    onMouseUp={this.handleSeekMouseUp}
                                />
                            </div>
                            <div className="toolbar-component">
                                <span role="img" onClick={this.handleToggleMuted}>{muted ? "🔇" : "🔊"} </span>
                                <input type='range' min={0} max={1} step='any' style={{width: "70px"}} value={volume} onChange={this.handleVolumeChange} />
                            </div>
                            <div className="toolbar-component" style={{float: "right"}}>
                                <input style={{display: "none"}} type="file" accept=".json" className="my-btn" ref={this.fileRef} onChange={this.loadFile}/>
                                <a className="my-btn" onClick={() => {
                                    this.fileRef.current.click();
                                }}>匯入</a>
                                <a className="my-btn" href={this.state.jsonUrl} download="SongList.json">匯出</a>
                            </div>
                        </div>
                        <div className="playlist-tab">
                            <ButtonToolbar aria-label="Toolbar with button groups">
                                {this.renderTabList()}
                                <Button size="sm" variant="light" onClick={this.handleAddTabDialog}>+</Button>
                            </ButtonToolbar>
                        </div>
                        <table className={"song-list"}>
                            <thead>
                                <tr style={{display: "grid"}}>
                                    <th>Title<span style={{float: "right", background: "gray"}} onClick={this.handleAddSongToTabActivedDialog}>Add Song</span></th>
                                    
                                </tr>
                            </thead>
                            <tbody className="song-list-body">
                                {this.renderSongList()}
                            </tbody>
                        </table>
                    </div>
                </div>
                <div className="player">
                    <ReactPlayer ref={this.ref}
                        url={url}
                        pip={pip}
                        playing={playing}
                        controls={controls}
                        light={light}
                        loop={loop}
                        playbackRate={playbackRate}
                        volume={volume}
                        muted={muted}
                        onReady={() => console.log('onReady')}
                        onStart={() => console.log('onStart')}
                        onPlay={this.handlePlay}
                        onEnablePIP={this.handleEnablePIP}
                        onDisablePIP={this.handleDisablePIP}
                        onPause={this.handlePause}
                        onBuffer={() => console.log('onBuffer')}
                        onSeek={e => console.log('onSeek', e)}
                        onEnded={this.handleEnded}
                        onError={e => console.log('onError', e)}
                        onProgress={this.handleProgress}
                        onDuration={this.handleDuration}/>
                </div>

            </div>
            
        );
    }
}

export default Main;